import React from "react";
import GaugeChart from "react-gauge-chart";

function Chart4() {
  const chartStyle = {
    // height: "400px",
    // width: "400px",
    backgroundColor: "#282c34"
  };

  return (
    <div style={{  }}>
      <GaugeChart
        id="gauge-chart5"
        nrOfLevels={420}
        arcsLength={[0.3, 0.5, 0.2]}
        colors={["#5BE12C", "#F5CD19", "#EA4228"]}
        
        // percent={0.37}
        // arcPadding={0.02}
        style={chartStyle}
      />
    </div>
  );
}

export default Chart4;
